if ($(window).width() <= 768) {
    $('#pc-games-rec-box').removeClass('swiper--pagination-hidden');
}

else {
    $('#pc-games-rec-box').addClass('swiper--pagination-hidden');
}

$(window).resize(function () {
    if ($(window).width() <= 768) {     
        $('#pc-games-rec-box').removeClass('swiper--pagination-hidden');
    }
    
    else {
        $('#pc-games-rec-box').addClass('swiper--pagination-hidden');
    }
});

var PCGamesRec = new Swiper('#pc-games-rec-box', {
    slidesPerView: 6,
    slidesPerGroup: 6,
    spaceBetween: 15,

    autoplay: {
        delay: 4500,
        disableOnInteraction: false,
    },

    pagination: {
        el: '#pc-games-rec-box .swiper-pagination',
        clickable: true,
    },
    
    breakpoints: {
        480: {
            slidesPerView: 3,
            slidesPerGroup: 3,
        },
        768: {
            slidesPerView: 4,
            slidesPerGroup: 4,
        },
    },
});